import {
    Component,
    Host,
    h,
    Prop,
    Event,
    EventEmitter,
    State,
    Listen,
    Method,
    Element,
} from '@stencil/core';
import { PromptMessage, MessageRole } from '../../../types/prompt';
import { fetchTranslations } from '../../../utils/translation';

/**
 * @category Prompt
 * @description A complete conversational interface container that combines a message thread, input field, and header. Provides a full-featured chat experience with message management, threading, and customizable UI elements.
 * @slot prompt-container-header - Custom content displayed above the message thread
 * @slot prompt-container-footer - Custom content displayed below the message input
 */
@Component({
    tag: 'atui-prompt-container',
    shadow: false,
})
export class AtuiPromptContainer {
    @Element() el: HTMLElement;

    /**
     * Array of messages to display in the conversation thread
     */
    @Prop({ mutable: true }) messages: PromptMessage[] = [];

    /**
     * Title displayed in the header section
     */
    @Prop() header_title: string = 'AI Assistant';

    /**
     * Subtitle displayed below the title in the header
     */
    @Prop() subtitle: string;

    /**
     * Placeholder text for the input field
     */
    @Prop() placeholder: string = 'Type your message here...';

    /**
     * Error text displayed when invalid is set via max length
     */
    @Prop()
    error_text: string;

    /**
     * Shows loading state and disables input
     */
    @Prop() loading: boolean = false;

    /**
     * Disables all interactions with the container
     */
    @Prop() disabled: boolean = false;

    /**
     * Controls visibility of the header section
     */
    @Prop() show_header: boolean = true;

    /**
     * Controls visibility of the "New Thread" button in the header
     */
    @Prop() show_new_thread_button: boolean = true;

    /**
     * Maximum character length for input messages
     */
    @Prop() max_message_length: number = 2000;

    /**
     * Display voting actions for assistant messages
     */
    @Prop() enable_vote: boolean = true;

    /**
     * Display copy action for assistant messages
     */
    @Prop() enable_copy: boolean = true;

    /**
     * Display edit action for user messages
     */
    @Prop() enable_edit: boolean = false;

    @State() currentInput: string = '';
    @State() inputInvalid: boolean = false;
    @State() inputError: string = '';
    @State() isSendEnabled: boolean = true;
    @State() translations: any = {};

    /**
     * Emits when a message should be sent
     */
    @Event()
    atuiSubmit: EventEmitter<string>;

    /**
     * Emits when the stop button is clicked
     */
    @Event()
    atuiStop: EventEmitter<void>;

    /**
     * Emitted when the "New Thread" button is clicked
     */
    @Event() atuiNewThread: EventEmitter<void>;

    /**
     * Emitted when a message copy action is requested
     */
    @Event() atuiMessageCopy: EventEmitter<{
        messageId: string;
        content: string;
    }>;

    /**
     * Emitted when a message retry action is requested
     */
    @Event() atuiMessageRetry: EventEmitter<{ messageId: string }>;

    /**
     * Emitted when a message edit action is requested
     */
    @Event() atuiMessageEdit: EventEmitter<{
        messageId: string;
        content: string;
    }>;

    /**
     * Emitted when a message vote action is requested
     */
    @Event() atuiMessageVote: EventEmitter<{
        messageId: string;
        score: number;
    }>;

    private inputComponent: HTMLAtuiPromptInputElement;

    async componentWillLoad() {
        this.translations = await fetchTranslations(this.el);
    }

    @Listen('atuiSubmit')
    handleInputSubmit(event: CustomEvent<string>) {
        const content = event.detail.trim();
        if (content && this.isSendEnabled && !this.disabled && !this.loading) {
            this.handleSubmit(content);
        }
    }

    @Listen('atuiMessageCopy')
    handleMessageCopy(
        event: CustomEvent<{ messageId: string; content: string }>,
    ) {
        this.atuiMessageCopy.emit(event.detail);
    }

    @Listen('atuiMessageRetry')
    handleMessageRetry(event: CustomEvent<{ messageId: string }>) {
        this.atuiMessageRetry.emit(event.detail);
    }

    @Listen('atuiMessageEdit')
    async handleMessageEdit(
        event: CustomEvent<{ messageId: string; content: string }>,
    ) {
        this.atuiMessageEdit.emit(event.detail);
        if (this.inputComponent) {
            this.inputComponent.value = event.detail.content;
            await this.focusInput();
        }
    }

    @Listen('atuiMessageVote')
    handleMessageVote(
        event: CustomEvent<{ messageId: string; score: number }>,
    ) {
        event.stopPropagation();
        const messageIndex = this.messages.findIndex(
            (msg) => msg.id === event.detail.messageId,
        );
        if (messageIndex !== -1) {
            const updatedMessages = [...this.messages];
            updatedMessages[messageIndex] = {
                ...updatedMessages[messageIndex],
                vote_status: event.detail.score,
            };
            this.messages = updatedMessages;
        }
    }

    /**
     * Programmatically add a message to the conversation thread
     * @param role - The message role
     * @param content - The message content
     */
    @Method()
    async addMessage(role: MessageRole, content: string): Promise<void> {
        const message: PromptMessage = {
            id: Date.now().toString(),
            role,
            content,
        };
        this.messages = [...this.messages, message];
    }

    /**
     * Append content to the last message in the thread (useful for streaming responses)
     * @param content - The content to append
     * @returns Promise<boolean> - Returns true if successful, false if no messages exist or last message is not from assistant
     */
    @Method()
    async appendToLastMessage(content: string): Promise<boolean> {
        if (this.messages.length === 0) return false;

        const lastMessage = this.messages[this.messages.length - 1];
        if (lastMessage.role !== 'assistant') return false;

        lastMessage.content += content;
        this.messages = [...this.messages];
        return true;
    }

    /**
     * Start a new conversation thread by clearing all messages and resetting state
     */
    @Method()
    async newThread(): Promise<void> {
        this.messages = [];
        this.currentInput = '';
        this.isSendEnabled = true;
        this.atuiNewThread.emit();
        setTimeout(() => this.focusInput(), 100);
    }

    /**
     * Control the send button state programmatically
     * @param enabled - Whether the send functionality should be enabled
     */
    @Method()
    async setSendEnabled(enabled: boolean): Promise<void> {
        this.isSendEnabled = enabled;
    }

    /**
     * Programmatically focus the input field
     */
    @Method()
    async focusInput(): Promise<void> {
        if (this.inputComponent) {
            setTimeout(() => {
                this.inputComponent.focus();
            }, 0);
        }
    }

    private handleSubmit = async (content: string) => {
        if (!content || this.disabled || this.loading || !this.isSendEnabled) {
            return;
        }

        this.isSendEnabled = false;

        await this.addMessage('user', content);
        this.atuiSubmit.emit(content);
    };

    private handleStop = () => {
        this.atuiStop.emit();
    };

    private handleNewThread = async () => {
        await this.newThread();
    };

    private renderHeader() {
        if (!this.show_header) return null;

        const newThreadText =
            this.translations?.ATUI?.PROMPT?.NEW_THREAD || 'New Thread';

        return (
            <atui-header
                size="h3"
                header_title={this.header_title}
                subtitle={this.subtitle}
                border={true}
                data-name="prompt-container-header"
            >
                {this.show_new_thread_button && (
                    <atui-button
                        slot="actions"
                        size="sm"
                        type="secondaryText"
                        onClick={this.handleNewThread}
                        disabled={this.loading}
                        data-name="new-thread-button"
                    >
                        {newThreadText}
                    </atui-button>
                )}
            </atui-header>
        );
    }

    private renderFooter() {
        return (
            <div class="p-4">
                <atui-prompt-input
                    ref={(el) => (this.inputComponent = el)}
                    placeholder={this.placeholder}
                    in_progress={this.loading}
                    max_length={this.max_message_length}
                    error_text={this.error_text}
                    data-name="prompt-container-input"
                    onAtuiSubmit={(event) => this.handleSubmit(event.detail)}
                    onAtuiStop={() => this.handleStop()}
                />
            </div>
        );
    }

    render() {
        return (
            <Host
                class="flex h-full w-full flex-col overflow-hidden"
                data-name="prompt-container"
            >
                {this.renderHeader()}
                <slot name="prompt-container-header" />
                <div
                    class="min-h-0 flex-1"
                    data-name="thread-wrapper"
                >
                    <atui-prompt-thread
                        messages={this.messages}
                        loading={this.loading}
                        auto_scroll={true}
                        enable_vote={this.enable_vote}
                        enable_copy={this.enable_copy}
                        enable_edit={this.enable_edit}
                        data-name="container-thread"
                    />
                </div>
                <div class="flex flex-col gap-4">
                    {this.renderFooter()}
                    <slot name="prompt-container-footer" />
                </div>
            </Host>
        );
    }
}
